@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
html,body,#root{width:100%;height:100%}body{background:rgb(17,17,17);background:rgb(var(--background-0));font-family:"Source Sans Pro",sans-serif;font-size:16px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-ms-overflow-style:none;margin:0;overflow:-moz-scrollbars-none;color:rgb(206,206,206);color:rgb(var(--color-0))}::-webkit-scrollbar{width:0 !important}*{cursor:default}a,.clickable{cursor:pointer}a *,.clickable *{cursor:pointer}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.accent{color:rgb(3,155,229);color:rgb(var(--accent));font-weight:700}:root{--background-0: 17,17,17;--background-1: 25,25,25;--color-0: 206,206,206;--color-icon: 78,78,78;--accent: 3,155,229}[data-theme=dark]{--background-0: 17,17,17;--background-1: 25,25,25;--color-0: 206,206,206;--color-icon: 78,78,78;--accent: 3,155,229}[data-theme=light]{--background-0: 93,93,93;--background-1: 140,140,140;--color-0: 42,42,42;--color-icon: 193,193,193;--accent: 120,61,168}.rh-main{width:100%;height:100%}
.div{display:flex}.div.col{flex-direction:column}.div.row{flex-direction:row}.div.align-center{align-items:center}.div.align-stretch{align-items:stretch}.div.align-start{align-items:flex-start}.div.align-end{align-items:flex-end}.div.align-self-center{align-self:center}.div.align-self-stretch{align-self:stretch}.div.align-self-end{align-self:flex-end}.div.justify-center{justify-content:center}.div.justify-stretch{justify-content:stretch}.div.justify-end{justify-content:flex-end}.div.justify-space-between{justify-content:space-between}.div.justify-space-evenly{justify-content:space-evenly}.div.fill-parent{flex:1 1;align-self:stretch}
.bg-img-layer{position:absolute;left:0;top:0;width:100%;height:100%;background-size:cover;background-position:left top;background-repeat:no-repeat}@media screen and (max-width: 650px){.bg-img-layer{width:120%}}
.splash-loader{width:100%;height:100%;position:relative;overflow:hidden}.splash-loader .splash-loader-container{width:100vw;height:100vh;position:absolute;color:#fff;left:0;top:0}.splash-loader .splash-loader-container .background-container{height:100vh;flex:1 1}.splash-loader .splash-loader-container .background-container .background{position:absolute;overflow:hidden;height:100vh;width:100vw;left:0;top:0;opacity:.3;background:rgb(var(--background-0));transform:scale(1.1)}.splash-loader .splash-loader-container .percentage-text{font-size:50px;margin-right:30px;z-index:99}.splash-loader .splash-loader-container .loading-text-container{height:100vh;z-index:99;overflow:hidden;background:rgb(var(--background-0));display:flex;align-items:center;justify-content:flex-end}.splash-loader .splash-loader-container .loading-text{font-size:40px;padding-right:18px}
